function _isValid(value) {
    return value.length > 0
}

let initialState = {
    messages: [
      { id: 1, message: 'hilol' },
      { id: 2, message: 'h2' },
      { id: 3, message: 'h3' }
    ],
    dialogs: [
      { id: 1, name: "Valer" },
      { id: 2, name: "Pam" }
    ]
  }

const dialogReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'dialogs/ADD-MESSAGE': 
        //if (_isValid(state.newMessageText)) {
            return {
                ...state, 
                // newMessageText: '',
                messages: [
                    ...state.messages, 
                    {id: 5, message: action.newMessageText}
                ]
            }
        //}
        default:
            return state
    }
}

export function addMessageActionCreator(newMessageText) {
    return {
        type: 'dialogs/ADD-MESSAGE',
        newMessageText
    }
}

export default dialogReducer