import { usersAPI } from '../API/API'
import { stopSubmit } from 'redux-form'

let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    password: null,
    rememberMe: false,
    captcha: false
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'auth/SET-USER-DATA':
            return {
                ...state,
                ...action.data
            }
        case 'auth/SET-LOGIN':
            return {
                isAuth: true
            }
        default:
            return state
    }
}

export function setAuthUserDataAccept(userId, email, login, isAuth) {
    return {
        type: 'auth/SET-USER-DATA',
        data: { userId, email, login, isAuth }
    }
}

export function setAuthLoginAccept(email, password, rememberMe) {
    return {
        type: 'auth/SET-LOGIN',
        data: { email, password, rememberMe }
    }
}

export const setAuthLogin = (email, password, rememberMe) =>
    async (dispatch) => {
        let response = await usersAPI.setAuthLogin(email, password, rememberMe)
        if (response.data.resultCode === 0) {
            dispatch(setAuthUserData())
        } else {
            let message = response.data.messages.length > 0 ? response.data.messages[0] : 'some error'
            dispatch(stopSubmit('login', { _error: message }))
        }
    }


export const setAuthLoginOut = () =>
    async (dispatch) => {
        let response = await usersAPI.setAuthLoginOut()
        if (response.data.resultCode === 0) {
            dispatch(setAuthUserData(null, null, null, false))
        }
    }


export const setAuthUserData = () =>
    async (dispatch) => {
        let response = await usersAPI.setAuthUserData()
        if (response.data.resultCode === 0) {
            let { id, email, login } = response.data.data
            dispatch(setAuthUserDataAccept(id, email, login, true))
        }
    }


export default authReducer

