import { usersAPI } from '../API/API'
import {updateObjArr} from '../../src/Utils/Validators/helper'

let initialState = {
    users: [],
    pagesSizes: 10,
    totalCount: 0,
    currentPage: 1,
    isFetching: false,
    followingProgress: []
}

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'users/FOLLOW':
            return {
                ...state,
                users: updateObjArr(state.users, action.userId, 'id', {followed: true})
            }
        case 'users/UNFOLLOW':
            return {
                ...state,
                users: updateObjArr(state.users, action.userId, 'id', {followed: false})
            }
        case 'users/SET-USERS':
            return { ...state, users: action.users }
        case 'users/SET-CURRENT-PAGE':
            return { ...state, currentPage: action.currentPage }
        case 'users/SET-TOTAL-USERS-COUNT':
            return { ...state, totalCount: action.totalCount }
        case 'users/TOGGLE-IS-FETCHING':
            return { ...state, isFetching: action.isFetching }
        case 'users/TOGGLE-IS-FOLLOWING':
            return {
                ...state,
                followingProgress: action.isFetching ?
                    [...state.followingProgress, action.userId]
                    :
                    state.followingProgress.filter(id => id != action.userId)
            }
        default:
            return state
    }
}

export function acceptFollow(userId) {
    return {
        type: 'users/FOLLOW',
        userId
    }
}

export function acceptUnfollow(userId) {
    return {
        type: 'users/UNFOLLOW',
        userId
    }
}

export function setUsers(users) {
    return {
        type: 'users/SET-USERS',
        users
    }
}

export function setCurrentPage(currentPage) {
    return {
        type: 'users/SET-CURRENT-PAGE',
        currentPage
    }
}

export function setUsersTotalCount(totalCount) {
    return {
        type: 'users/SET-TOTAL-USERS-COUNT',
        totalCount
    }
}

export function toggle(isFetching) {
    return {
        type: 'users/TOGGLE-IS-FETCHING',
        isFetching
    }
}

export function toggleFollowing(isFetching, userId) {
    return {
        type: 'users/TOGGLE-IS-FOLLOWING',
        isFetching,
        userId
    }
}

export const getUsers = (page, pagesSizes) => {
    return async (dispatch) => {
        dispatch(toggle(true))
        dispatch(setCurrentPage(page))
        let data = await usersAPI.getUsers(page, pagesSizes)
        dispatch(toggle(false))
        dispatch(setUsers(data.items))
        dispatch(setUsersTotalCount(data.totalCount))
        }
    }

export const followUnfollow = async (dispatch, userId, apiMethod, actionCreator) => {
    dispatch(toggleFollowing(true, userId))
    let response = await apiMethod(userId)
    if (response.data.resultCode == 0) {
        dispatch(actionCreator(userId))
    }
    dispatch(toggleFollowing(false, userId))
}


export const follow = (userId) => {
    return async (dispatch) => {
        let apiMethod = usersAPI.follow.bind(usersAPI)
        let actionCreator = acceptFollow
        followUnfollow(dispatch, userId, apiMethod, actionCreator)
    }
}

export const unfollow = (userId) => {
    return async (dispatch) => {
        let apiMethod = usersAPI.unfollow.bind(usersAPI)
        let actionCreator = acceptUnfollow
        followUnfollow(dispatch, userId, apiMethod, actionCreator)
    }
}

export default usersReducer