import { usersAPI, profileApi } from '../API/API'

function _isValid(value) {
    return value.length > 0
}


let initialState = {
    posts: [
        { id: 1, message: 'hi66', likesCount: 666 },
        { id: 2, message: 'lol_f', likesCount: 11 }
    ],
    profile: null,
    status: ''
}

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'profile/ADD-POST':
            //if (_isValid(state.newPostText)) {
            return {
                ...state,
                newPostText: '',
                posts: [
                    ...state.posts,
                    { id: 5, message: action.newPostText, likesCount: 0 }]
            }
        //}
        case 'profile/UPDATE-NEW-POST-TEXT':
            return {
                ...state,
                newPostText: action.newText
            }
        case 'profile/SET-USER-PROFILE':
            return { ...state, profile: action.profile }
        case 'profile/SET-STATUS':
            return { ...state, status: action.status }
        case 'profile/DELETE':
            return { ...state, posts: state.posts.filter(p => p.id != action.postId) }
        default:
            return state
    }
}

export function deletePost(postId) {
    return {
        type: 'profile/DELETE',
        postId
    }
}

export function addPostActionCreator(newPostText) {
    return {
        type: 'profile/ADD-POST',
        newPostText
    }
}

export function setUsersProfileAccept(profile) {
    return {
        type: 'profile/SET-USER-PROFILE',
        profile
    }
}

export function setStatus(status) {
    return {
        type: 'profile/SET-STATUS',
        status
    }
}

export const setUsersProfile = (profile) =>
    async (dispatch) => {
        let response = await usersAPI.setUsersProfile(profile)
        dispatch(setUsersProfileAccept(response.data))
    }

export const getStatus = (userId) =>
    async (dispatch) => {
        let response = await profileApi.getStatus(userId)
        dispatch(setStatus(response.data))
    }

export const updateStatus = (status) =>
    async (dispatch) => {
        let response = await profileApi.updateStatus(status)
        if (response.data.resultCode === 0) {
            dispatch(setStatus(status))
        }
    }

export default profileReducer