import React from 'react'
import { Field, reduxForm } from 'redux-form'
import { Input } from '../Common/Preloader/FormControl/FrontControls'
import { required } from '../../Utils/Validators/validators'
import { connect } from 'react-redux'
import { setAuthLogin } from '../../Redux/Auth-reducer'
import { Redirect } from 'react-router-dom'
import style from '../Common/Preloader/FormControl.module.css'

const LoginForm = ({handleSubmit, error}) => {
    return (
        <form onSubmit={handleSubmit}>
            <div className="">
                <Field validate={[required]} 
                placeholder={"Email"} name={'email'} 
                component={Input} />
            </div>
            <div className="">
                <Field validate={[required]} 
                placeholder={"Password"} 
                name={'password'} 
                component={Input} 
                type={'password'} />
            </div>
            <div className="">
                <Field 
                component={Input} 
                name={'rememberme'} 
                type={"checkbox"} /> remember me
        </div>
            {error && <div className={style.formSummaryError}>
                {error}
            </div>
            }
            <div className="">
                <button>Login</button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm({ form: 'login' })(LoginForm)


function Login(props) {
    const onSubmit = (formData) => {
        props.setAuthLogin(formData.email, formData.password, formData.rememberMe)
    }

    if (props.isAuth) {
        return <Redirect to={'/profile'} />
    }

    return <div className="">
        <h1>login</h1>
        <LoginReduxForm onSubmit={onSubmit} />
    </div>
}

let mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, { setAuthLogin })(Login)